<template>
  <div class="sections">
    <div class="pl_body">
      <Tabs :v-model="tabVal">
        <TabPane label="信美代偿记录" name="xinmei">
          <Row class="form_row">
              <Col :sm="22" :xs="24">
                  <Form :model="formSel" class="downMenu">
                      <FormItem>
                          <Input style="display:none;"></Input>
                          <Input v-model="formSel.input" icon="ios-search" placeholder="借款编号/客户姓名/手机号" @on-enter=""></Input>
                          <Button type="primary" @click="">查询</Button>
                      </FormItem>
                  </Form>
              </Col>
              <Col :sm="2" :xs="24"><div class="float_right">
                  <Button type="success" @click="exportData(2)"><Icon type="ios-download-outline"></Icon> 导出EXCEL</Button>
              </div></Col>
          </Row>
          <div class="table_show">
          <Table border :columns="columns1" :data="data1" ref="table"></Table></div>
          <Row :gutter="16" v-if="pageWin>=768">
              <Col :sm="5" :xs="24">
                  <div class="total">共{{total}}页</div>
              </Col>
              <Col :sm="19" :xs="24" class="txt_right">
                  <Page :total="total" show-elevator @on-change="changePage"></Page>
              </Col>
          </Row>
          <Page v-else :total="total" simple @on-change="changePage"></Page>
        </TabPane>
        <TabPane label="代理商代偿记录" name="agent">
          <Form :model="formSel" class="downMenu">
              <FormItem>
                  <Input style="display:none;"></Input>
                  <Input v-model="formSel.input" icon="ios-search" placeholder="借款编号/客户姓名/手机号" @on-enter=""></Input>
                  <Button type="primary" @click="">查询</Button>
              </FormItem>
          </Form>
          <div class="table_show">
          <Table border :columns="columns2" :data="data2" ref="table"></Table></div>
          <Row :gutter="16" v-if="pageWin>=768">
              <Col :sm="5" :xs="24">
                  <div class="total">共{{total}}页</div>
              </Col>
              <Col :sm="19" :xs="24" class="txt_right">
                  <Page :total="total" show-elevator @on-change="changePage"></Page>
              </Col>
          </Row>
          <Page v-else :total="total" simple @on-change="changePage"></Page>
        </TabPane>
        </Tabs>
    </div>

  </div>
</template>

<script>
export default {
  name: 'sections',
  data () {
    return {
        tbWidth:100,
        total:100,
        formSel:{
            input:''
        },
        tabVal:'xinmei',
        columns1: [
          {
            title: 'ID',
            key: 'planId'
          },
          {
            title: '借款编号',
            key: 'loanNo'
          },{
            title: '客户姓名',
            key: 'custName'
          },{
            title: '手机号',
            key: 'custMobile'
          },{
            title: '身份证号',
            key: 'idcard'
          },
          {
            title: '借款金额',
            key: 'loanAmount'
          },
          {
            title: '首付金额',
            key: 'downpayAmount'
          },
          {
            title: '借款时间',
            key: 'loanTime',
            width:104
          },
          {
            title: '省份',
            key: 'province'
          },
          {
            title: '城市',
            key: 'city'
          },
          {
            title: '总期数',
            key: 'totalStage'
          },
          {
            title: '当前期数',
            key: 'planStage'
          },
          {
            title: '应还时间',
            key: 'planLtm',
            width:104
          },
          {
            title: '应还本金',
            key: 'planPrp'
          },
          {
            title: '应还服务费',
            key: 'planSfe'
          },
          {
            title: '应还利息',
            key: 'planInt'
          },
          {
            title: '应还罚息',
            key: 'planPin'
          },
          {
            title: '应还违约金',
            key: 'd'
          },
          {
            title: '应还总金额',
            key: 'planTotal'
          },
          {
            title: '逾期天数',
            key: 'exceedDay'
          },
          {
            title: '代偿状态',
            key: 'cpsSt'
          },
          {
            title: '备注',
            key: 'clrType'
          }],
        data1: [],
        columns2:[{
          title:'代理商名称',
          key:'agentName'
        },
        {
          title:'法人姓名',
          key:'name'
        },
        {
          title:'手机号',
          key:'phone'
        },
        {
          title:'省市',
          key:'address'
        },
        {
          title:'代偿金额',
          key:'money'
        },
        {
          title:'代偿月份',
          key:'month'
        },
        {
          title:'代偿时间',
          key:'date'
        }],
        data2:[{
          agentName:'深圳高明有限公司',
          name:'张一河',
          phone:'13655556666',
          address:'广东省深圳市',
          money:'500.00',
          month:'2017-12',
          date:'2017-02-05 12:12:21'
        }]
        }
    },
    created:function(){
        if($(window).width()<768){
            var arr=this.columns1;
            for(var i=0;i<arr.length;i++){
                arr[i]['width']=this.tbWidth;
            }
            this.columns1=arr;
        }
    },
    computed :{
        pageWin(){
            return Math.max(window.innerWidth,document.documentElement.clientWidth,document.body.clientWidth);
        }
    },
    mounted(){
        this.loadContent(1)
    },
    methods: {
        exportData () {
            /*if (type === 1) {
                this.$refs.table.exportCsv({
                    filename: '原始数据'
                });
            } else*/ if (type === 2) {
                this.$refs.table.exportCsv({
                    filename: '排序和过滤后的数据',
                    original: false
                });
            } /*else if (type === 3) {
                this.$refs.table.exportCsv({
                    filename: '自定义数据',
                    columns: this.columns8.filter((col, index) => index < 4),
                    data: this.data7.filter((data, index) => index < 4)
                });
            }*/
        },
        loadContent(page){
          var firstResult = (parseInt(page) - 1) * 10;
          var pageSize = 10;
          var that=this;

          $.ajax({
            url : "/compensatory/queryAllCompensatoryInfos",
            type : "post",
            cache : false,
            dataType : "json",
            xhrFields: {
              withCredentials: true
            },
            data : {
              cpsSt : 2,//已代偿
              pageStart :firstResult,
              pageSize :pageSize
            },
            contentType : "application/x-www-form-urlencoded; charset=utf-8",

            success : function(data) {

              console.log(JSON.stringify(data))
              that.total = data.count

              if(data.resCode == 1){
                that.total = data.count.count;
                data.result.forEach(function(element) {
                  if (element.cpsSt == 0){
                    element.cpsSt = '未到代偿日'
                  }else if(element.cpsSt == 1){
                    element.cpsSt = '待代偿'
                  }else if(element.cpsSt == 2){
                    element.cpsSt = '代偿成功'
                  }
                }, this);
                that.data1 = data.result;
              }
            }
          });
        },
        changePage(){
            /*this.data1=this.*/
        }
    }
}
</script>



